import React from 'react';
import './App.css';

import author1 from './images/author1.jpeg';
import adv from './images/advmain.jpg';


function LatestArticle(props) {
  return (
    <>
      <br/>
      <div class="grid-container"> 
      
        <div class="article-box">
            <span class="article color-change">Fresh Article </span> <span class="date">July 27,2020</span>
            <br/><br/>
            <span class="latest-artical-heading  color-change">Using Trello as a Super Simple CMS</span>
            <br/><br/>
            <div class="author-info-panel">
                <div style={{position:'relative',width:'25%'}}>
                    <a href=""><img class="author-img" src={author1} alt=""/></a>
                    <svg class="half-circle" width="80px" height="80px">
                        <use xlinkHref="#half-circle"></use>
                    </svg>
                </div>
                <div>
                    <p class="author-comments">Author</p>
                    <p class="author-comments-designation">Peter Paul</p>
                </div>
                <div style={{width: '53%'}}> 
                    <p class="author-comments">Comments</p>
                    <p class="author-comments-designation" >Start Conversation</p>
                </div>
            </div>
            <br/>
            <div class="article-content-box">
                <p>Sometimes our sites need a little sprinkling of content management. Not always. Not a lot. But a bit. 
                The CMS market is thriving with affordable, approachable products, so were not short of options. 
                </p>
                <p>Thankfully, it is a very different world to the one that used to force companies to splash out a ga-jillionty-one dollars 
                (not an exact cost: I rounded to the nearest bazillion) for an all-singing, all-dancing, all-integrating, all-personalizing, big-enterprise-certified CMS platform.</p>
                <p>Sometimes, though, its nice to use a </p>
            </div>
            <br/>
            <p class="read-article">Read Article</p>
            <br/>
            <button class="button-rnd color-change" style={{marginLeft:'0px'}}>API</button>
            <button class="button-rnd color-change">CMS</button>
            <button class="button-rnd color-change">JSON</button>
           
            <br/><br/><br/><br/>
        </div>
        <div style={{float: 'right' }}>
            <div class="adv-box" style={{marginTop:'420px'}}>
                <img src={adv} alt=""/>
                <br/><br/>
                <div class="adv-content-box">
                <p class="adv-title">sponsered</p>
                <p class="adv-description">Implement our SMTP service in under 5 minutes with our quick and easy 2-step sign-up process.</p>
                </div>
            </div>
        </div>
        </div>
      
    </>
  );
}

export default LatestArticle;