import React from 'react';
import './Card.css';

import aut1 from './images/author1mc.png';


function Card(props) {
  return (
    <>
        <div class="card">
            <span class="article-cm color-change">Article</span> <span class="date-cm">{props.date}</span>
            <br/>
            <span class="artical-heading-card  color-change">{props.title}</span>
            <div>
                <div class="author-info-panel-cm">
                    <div style={{position:'relative',width:'40%'}}>
                        <a href=""><img class="author-img-cm" src={props.imgsrc} alt=""/></a>
                    </div>
                    <div style={{width: '50%'}}>
                        <p class="author-comments-cm">Author</p>
                        <p class="author-comments-designation-cm">{props.autname}</p>
                    </div>
                </div>
                <button class="button-rnd color-change">{props.btn1}</button> <button class="button-rnd color-change">{props.btn2}</button>
            </div>
        </div>
    </>
  );
}

export default Card;