import React from 'react';
import './App.css';

import img1 from './images/MLogo.png';


function AdvBig(props) {
  return (
    <>
      
      <div class="adv-big">
        <div class="adv-big-top">
            <center>
                <p>our learning patners</p>
                <img src={img1} alt="" height="80px" width="150px"/>
            </center>
        </div>
        <div class="adv-big-bottom">
            <h3>Need some front-end development training?</h3>
            <p>Frontend Masters is the best place to get it. They have courses on all the most important front-end technologies, from React to CSS, from Vue to D3, and beyond with Node.js and Full Stack.</p>
        </div>
      </div>
      
    </>
  );
}

export default AdvBig;