import React from 'react';
import './App.css';


import author1 from './images/author1.jpeg';
import author2 from './images/author2.jpeg';
import author3 from './images/author3.jpeg';



function Content(props) {
  return (
    <>
        <br/><br/>
        <div class="article-box">
                <span class="article color-change">{props.artDes}</span> <span class="date">{props.artDate}</span>
            <br/>
            <span class="artical-heading  color-change">{props.artHeading1}<br/>{props.artHeading2}</span>
            
            <div class="author-info-panel">
                <div style={{position:'relative',width:'25%'}}>
                    <a href=""><img class="author-img" src={props.imgSrc} alt=""/></a>
                    <svg class="half-circle" width="80px" height="80px">
                        <use xlinkHref="#half-circle"></use>
                    </svg>
                </div>
                <div>
                    <p class="author-comments">Author</p>
                    <p class="author-comments-designation ">{props.autName}</p>
                </div>
                <div style={{width: '53%'}}> 
                    <p class="author-comments">Comments</p>
                    <p class="author-comments-designation" >Start Conversation</p>
                </div>
            </div>
            
            <div class="article-content-box">
                <p> {props.artContent1}</p>
                <p>{props.artContent2}</p>
            </div>
            
            <p class="read-article">{props.last}</p>
        </div>

      
    </>
  );
}

export default Content;