import React from 'react';
import './footer.css';

import img1 from './images/codepen.png';
import img2 from './images/flywheel.png';
import img3 from './images/footer3.png';

function Footer() {
  return (
    <>
      
      <center>
            <h2 class="newsletter"> The Newsletter</h2>
            <input class="newsletter-input" type="text" placeholder="lovetricks@internet.cool"/><button class="newsletter-btn">Subscribe</button>
            <br/><br/>
            <div class="sm-container">
                <div><span class="sm-name">Facebook</span><p class="sm-desc">@CSSTricks</p></div>
                <div><span class="sm-name">Twitter</span><p class="sm-desc">@CSS</p></div>
                <div><span class="sm-name">Youtube</span><p class="sm-desc">@realcsstricks</p></div>  
                <div><span class="sm-name">Instagram</span><p class="sm-desc">@real_css_tricks</p></div>
                <div><span class="sm-name">RSS</span><p class="sm-desc">Feed</p> </div> 
            </div>
        </center>

        <br/><br/><br/><br/>

        <div class="footer-container">
            <div class="footer-left">
                CSS-Tricks<span class="clr-multi">*</span> is created, written by, and maintained by <span class="color-change clr-white">Chris Coyier</span> and <span class="color-change clr-white">a team</span> of swell people. The tech stack for this site is fairly <span class="color-change clr-white">boring</span>. That's a good thing! I've used <span class="color-change clr-white">WordPress</span> since day one all the way up to v17, a decision I'm <span class="color-change clr-white">very happy with</span>. I also leverage <span class="color-change clr-white">Jetpack</span> for extra functionality and <span class="color-change clr-white">Local</span> for local development.
                <br/><br/>
                <span style={{fontSize:'16px'}}>*May or may not contain any actual "CSS" or "Tricks".</span>
            </div>
            <div class="footer-right">
                <h2>Other Nav</h2>
                <table >
                    <tr>
                        <td><span class="color-change">Contact</span></td>
                        <td><span class="color-change">About</span></td>
                    </tr>

                    <tr>
                        <td><span class="color-change">Guess Writing</span></td>
                        <td><span class="color-change">Jobs</span></td>
                    </tr>

                    <tr>
                        <td><span class="color-change">Advertise</span></td>
                        <td><span class="color-change">Lisence</span></td>
                    </tr>

                    <tr>
                        <td><span class="color-change">Subscribe</span></td>
                        <td><span class="color-change">All Authors</span></td>
                    </tr>

                </table>
            </div>
        </div>

        <br/><br/><br/><br/><br/><br/><br/><br/>

        <div class="footer-card-holder">
            <div>
                <img src={img1} alt="" height="30%" width="60%"/>
                <p> <span href="" class="color-change">CodePen</span> is a place to experiment, debug, and show off your HTML, CSS, and JavaScript creations.</p>
            </div>
            <div>
                <img src={img2} alt="" height="30%" width="60%"/>
            <p> CSS-Tricks is hosted by <span class="color-change">Flywheel</span>, the best WordPress hosting in the business, with a local development tool to match.</p>
            </div>
            <div>
                <img src={img3} alt="" height="50%" width="30%" />
            <p> <span class="color-change">ShopTalk</span> is a podcast all about front-end web design and development.</p>
            </div>
        </div>
      
    </>
  );
}

export default Footer;