import React from 'react';
import './Nav.css';





function Nav() {
  return (
    <>
                <nav class="nav-bar">
            <div class="nav-left">
                <a href="#">
                    <svg class="icon-logo-star" width="35px" height="35px">
                    <use xlinkHref="#icon-logo-star"></use>
                    </svg> &nbsp;
                    <svg class="icon-logo-text" width="214px" height="35px">
                    <use xlinkHref="#logo"></use>
                    </svg>
                </a>
            </div>
            <div class="nav-mid">
                <ul class="nav-mid-items">
                    <li class="articles">
                    <a href="/archives/">
                        <svg width="30" height="30">
                        <use xlinkHref="#icon-nav-articles"></use>
                        </svg>
                        <span>Articles</span>
                    </a>
                    </li>
                    <li class="videos">
                    <a href="/video-screencasts/">
                        <svg width="30" height="30">
                        <use xlinkHref="#icon-nav-videos"></use>
                        </svg>
                        <span>Videos</span>
                    </a>
                    </li>
                    <li class="almanac">
                    <a href="/almanac/">
                        <svg width="30" height="30">
                        <use xlinkHref="#icon-nav-almanac"></use>
                        </svg>
                        <span>Almanac</span>
                    </a>
                    </li>
                    <li class="newsletter">
                    <a href="/newsletters/">
                        <svg width="30" height="30">
                        <use xlinkHref="#icon-nav-newsletter"></use>
                        </svg>
                        <span>Newsletter</span>
                    </a>
                    </li>
                    <li class="guides">
                    <a href="/guides/">
                        <svg width="30" height="30">
                        <use xlinkHref="#icon-nav-guides"></use>
                        </svg>
                        <span>Guides</span>
                    </a>
                    </li>
                    <li class="books">
                    <a href="/books/">
                        <svg width="30" height="30">
                        <use xlinkHref="#icon-nav-books"></use>
                        </svg>
                        <span>Books</span>
                    </a>
                    </li>
                    <li class="account">
                    <a href="/product/membership/">
                        <svg width="30" height="30">
                        <use xlinkHref="#icon-nav-account"></use>
                        </svg>
                        <span>Account</span>
                    </a>
                    </li>
                </ul>

            </div>
            <div class="nav-right">
                <div class="search-bar-container">
                    <input type="search" name="search-bar" value="" placeholder="e.g. flexbox"/>
                    <button type="button" name="search-btn">
                    <svg class="icon-search" width="20px" height="20px">
                    <use xlinkHref="#icon-search"></use>
                    </svg>
                    </button>
                </div>
            </div>
        </nav>
        
    </>
  );
}

export default Nav;