import React from 'react';
import './App.css';


function AdCard(props) {
  return (
    <>
      
      <div className="adv-box">
      <img src={props.imgsrc} alt=""/>
        <br/><br/>
        <div className="adv-content-box">
        <p className="adv-title">sponsered</p>
            <p className="adv-description">{props.adContent}</p>
        </div>
    </div>
      
    </>
  );
}

export default AdCard;